import React from 'react';
import { MDBCol, MDBCardImage } from "mdbreact";

class Block extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
    };

    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({});
  }

  componentDidMount = () => {

  }

  render = () => {

    const { config } = this.props;
    let optsMDBCol  = config.attributes;
    
    if(config.innerImage){
        let { attributes } = config.innerImage;
        return (
             <MDBCol {...optsMDBCol}>
                   <MDBCardImage {...attributes}></MDBCardImage>
             </MDBCol>
        );
    }else{
        let innerTextHTML = "";
        config.innerTextHTML.forEach(string => {
          innerTextHTML += string;
        });

        return (
             <MDBCol {...optsMDBCol} dangerouslySetInnerHTML={{ __html: innerTextHTML }}></MDBCol>
        );
    }
  }
}

export default Block;