import React from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";
import AnchorLink from 'react-anchor-link-smooth-scroll'

class FooterPage extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            collapse: false,
            isWideEnough: false,
        };
    }

    componentDidMount = () => {

    }

    render = () => {
        const { config } = this.props;
        let innerTextHTML = "";
        config.innerTextHTML.forEach(string => {
            innerTextHTML += string;
        });

        return (
            <MDBFooter {...config.attributes}>
                <MDBContainer fluid className="text-center text-md-left">
                    <MDBRow>
                        <MDBCol md="6">
                            <h6 className="font-weight-bold">
                                <strong>{config.title}</strong>
                            </h6>
                            <hr className={'deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto'} style={{width: 60}}></hr>
                            <div dangerouslySetInnerHTML={{ __html: innerTextHTML }}></div>
                        </MDBCol>
                        <MDBCol md="3">
                            <h6 className="font-weight-bold">
                                <strong>{config.links.title}</strong>
                            </h6>
                            <hr className={'deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto'} style={{width: 60}}></hr>
                            <ul {...config.links.attributes}>
                                {Object.keys(config.links.items).map(key => {
                                    return (
                                        <li key={key} className="list-unstyled">
                                            <AnchorLink href={config.links.items[key].url}>{config.links.items[key].name}</AnchorLink>
                                        </li>
                                    )
                                })}
                            </ul>
                        </MDBCol>
                        <MDBCol md="3">
                            <h6 className="font-weight-bold">
                                <strong>{config.social.title}</strong>
                            </h6>
                            <hr className={'deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto'} style={{width: 60}}></hr>
                            <ul {...config.social.attributes}>
                            {Object.keys(config.social.items).map(key => {
                                    return (
                                        <li key={key} className="Footer-item list-unstyled col-4">
                                            <a className="Footer-link waves-effect waves-light"><i className={"fab " + config.social.items[key].className}></i></a>
                                        </li>
                                    )
                                })}
                                </ul>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
                <div className="footer-copyright text-center py-3">
                    <MDBContainer fluid>
                        {/* &copy; {new Date().getFullYear()} Copyright: <a href="https://www.MDBootstrap.com"> MDBootstrap.com </a> */}
                    </MDBContainer>
                </div>
            </MDBFooter>
        );
    }
}

export default FooterPage;