import React from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBNavLink, MDBMask, MDBView, MDBBtn } from 'mdbreact';
import { MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBCarouselCaption } from "mdbreact";
import { BrowserRouter as Router } from 'react-router-dom';
import AnchorLink from 'react-anchor-link-smooth-scroll'

class Navbar extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      collapse: false,
      isWideEnough: false,
    };

    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse,
    });
  }

  componentDidMount = () =>  {
  }

  render() {
    const { config } = this.props;
    const { mdbNavbarNav } = config;
    const { mdbCarousel } = config;

    let optsMDBNavbar = config.mdbNavbar;
    let optsMDBNavbarNavLinks;
    let optsMDBNavbarNavButtons;

    let optsMDBView = config.mdbView;
    let optsMDBMask = config.mdbView.mdbMask.attributes;

    let innerTextHTMLMDBMask = "";
    config.mdbView.mdbMask.innerTextHTML.forEach(string => {
      innerTextHTMLMDBMask += string;
    });

    let optsMDBCarousel = mdbCarousel.attributes;

    let displayIntro;

    let innerNavbarNavLinks;
    let innerNavbarNavButtons;

    if (config.display.active) {
      if (config.display.type === "carousel") {
        displayIntro = (<MDBCarousel {...optsMDBCarousel}>
          <MDBCarouselInner>
            {Object.keys(mdbCarousel.items).map(key => {
              let mdbCarouselItemAttributes = mdbCarousel.items[key].attributes;
              let mdbCarouselItemMask = mdbCarousel.items[key].mdbMask;
              let itemMask;
              let captionTextHTML;
              if (mdbCarouselItemMask) {
                //Agregar Iteración para texto
                itemMask = (<MDBMask {...mdbCarouselItemMask.attributes} />);
                if(mdbCarouselItemMask.innerTextHTML){
                  captionTextHTML = mdbCarouselItemMask.innerTextHTML;
                }
              }
              return (
                <MDBCarouselItem key={key} itemId={++key}>
                  <MDBView>
                    <img {...mdbCarouselItemAttributes} />
                    {itemMask}
                  </MDBView>
                  <MDBCarouselCaption dangerouslySetInnerHTML={{ __html: captionTextHTML }}>
                  </MDBCarouselCaption>
                </MDBCarouselItem>
              )
            })}
          </MDBCarouselInner>
        </MDBCarousel>)
      } else {
        displayIntro = (
          <MDBView {...optsMDBView}>
            <MDBMask {...optsMDBMask} dangerouslySetInnerHTML={{ __html: innerTextHTMLMDBMask }}>
            </MDBMask>
          </MDBView>);
      }
    }

    if (mdbNavbarNav.links) {

      optsMDBNavbarNavLinks = mdbNavbarNav.links.attributes;

      innerNavbarNavLinks = (<MDBNavbarNav {...optsMDBNavbarNavLinks}>
        {Object.keys(mdbNavbarNav.links.items).map(key => {
          return (
            <MDBNavItem key={key} active={mdbNavbarNav.links.items[key].active}>
              <MDBNavLink to=""><AnchorLink href={mdbNavbarNav.links.items[key].url}>{mdbNavbarNav.links.items[key].name}</AnchorLink></MDBNavLink>
            </MDBNavItem>
          )
        })}
      </MDBNavbarNav>)
    }

    if (mdbNavbarNav.buttons) {

      optsMDBNavbarNavButtons = mdbNavbarNav.buttons.attributes;

      innerNavbarNavButtons = (<MDBNavbarNav  {...optsMDBNavbarNavButtons}>
        {Object.keys(mdbNavbarNav.buttons.items).map(key => {
          let buttonConfig = mdbNavbarNav.buttons.items[key];
          return (
            <MDBNavItem key={key} active={buttonConfig.active}>
              <MDBBtn {...buttonConfig.attributes}>{buttonConfig.name}</MDBBtn>
            </MDBNavItem>
          )
        })}
      </MDBNavbarNav>)
    }

    let innerNavbarNavLeftPlace;
    let innerNavbarNavRightPlace;

    if (mdbNavbarNav.links.place == "right") {
      innerNavbarNavRightPlace = innerNavbarNavLinks;
      innerNavbarNavLeftPlace = innerNavbarNavButtons;
    } else {
      innerNavbarNavRightPlace = innerNavbarNavButtons;
      innerNavbarNavLeftPlace = innerNavbarNavLinks;
    }

    let innerNavbarBrand;
    if(config.mdbNavbarBrand.image){
      innerNavbarBrand = (<img {...config.mdbNavbarBrand.image.attributes}/>);
    }else{
      innerNavbarBrand = (<strong>{config.mdbNavbarBrand.title}</strong>);
    }

    return (
      <React.Fragment>
        <Router>
          <MDBNavbar {...optsMDBNavbar}>
            <MDBNavbarBrand href="/"> {/*Agregar a configuración */}
              {innerNavbarBrand}
            </MDBNavbarBrand>
            {!this.state.isWideEnough && <MDBNavbarToggler onClick={this.onClick} />}
            <MDBCollapse isOpen={this.state.collapse} navbar>
              {innerNavbarNavLeftPlace}
              {innerNavbarNavRightPlace}
            </MDBCollapse>
          </MDBNavbar>
        </Router>
        <section id={'home'}>
        {displayIntro}
        </section>
      </React.Fragment>
    );
  }
}

export default Navbar;