import React, { Component } from 'react';
import Navbar from './components/mdb-navbar';
import Container from './components/mdb-container';
import FooterPage from './components/mdb-footer';

const navbarConfig = require("./config/config-navbar.json");
const containerConfig = require('./config/config-container.json');
const footerConfig = require("./config/config-footer.json");

class App extends Component {

  render() {
    return (
      <React.Fragment>
        <header>
          <Navbar config={navbarConfig}></Navbar>
        </header>
        <main className="App">
          <Container config={containerConfig}></Container>
        </main>
        <footer>
        <FooterPage  config={footerConfig}></FooterPage>
        </footer>
      </React.Fragment>

    );
  }
}

export default App;